
import { Vue } from "vue-class-component";
import { store } from "@/store";
import { Game } from "@/models/game";

export default class Rate extends Vue {
  private games: Game[] = [];

  mounted() {
    this.loadAllGames();
  }

  private loadAllGames() {
    this.games = store.state.ownedGames;
  }
}
